import { defineStore } from "pinia";
import boardDataService from "../service/BoardDataService";
import Paging from "@/models/Paging";

const useBoardStore = defineStore('boardState',{
  state:()=>({
    boards:[], //  
    paging: new Paging()
  }),
  getters:{},
  actions: {
    async getBoards() {
      await boardDataService
        .getPagingList()
        .then((response) => {
          this.boards = response.data.boards
          this.paging = new Paging(response.data.page)

          console.log(response.data)
          console.log(this.boards)
        })
        .catch((e) => {
          console.log(e)
        })
    },
    async getPagingBoards(param) {
      console.log('onClickPaging ' + param)
      const splits = String(param).split('?')

      await boardDataService
        .getPagingList(splits[0], '?' + splits[1])
        .then((response) => {
          this.boards = response.data.boards
          this.paging = new Paging(response.data.page)
        })
        .catch((e) => {
          console.log(e)
        })
    },
    async deleteBoard(bid) {
      console.log('deleteBoard() ' + bid)

      await boardDataService
        .remove(Number(bid))
        .then((response) => {
          console.log(response.data + '')
        })
        .catch((e) => {
          console.log(e)
        })
    },
    async writeBoard(board) {
      console.log('Wirte ' + board.btitle)
      console.log('Wirte ' + board.bcontent)
      console.log('Wirte ' + board.bname)

      await boardDataService
        .write(board)
        .then((response) => {
          console.log('Write ' + response.data)
        })
        .catch((e) => {
          console.log('Write ' + e)
        })
    },
    async updateBoard(board) {
      console.log(' ' + board)

      await boardDataService
        .update(board.bid, board)
        .then((response) => {
          console.log('update ' + response.data)
        })
        .catch((e) => {
          console.log('Write ' + e)
        })
    },
    async getBoard(bid) {
      await boardDataService
        .get(Number(bid))
        .then((response) => {
          console.log(response.data)
          this.boards = response.data
        })
        .catch((e) => {
          console.log('Board ' + e)
        })
    }
  }
})

export default useBoardStore
